.create {
  background: lightgray;
  overflow-x: hidden;
  overflow-y: hidden;
  width: 1300px;
  height: 910px;
  color: green;
  font-family: emirates;
}
.create li {
  list-style: none;
  overflow-y: scroll;
}
.create a {
  color: green;
  list-style: none;
  text-decoration: none;
}
.listing {
  position: relative;
  /* margin-top: 50px; */
  overflow-y: scroll !important;
}
.coc {
  overflow-y: visible !important;
  z-index: 1;
  color: green;
  height: 140vh;
    width: 1200px;
  font-family: emirates;
  background: lightgray;
  /* overflow-x: hidden !important; */
  overflow-y: scroll;
  text-decoration: none;
}
.coc li{
    text-decoration: none;
    list-style: none;
    margin-top: 50px;
    text-align: center;
}
.coc a {
  color: green;
  list-style: none;
  text-decoration: none;
}
@font-face {
  font-family: emirates;
  src: url(../../multimedia/Emirates-Bold.ttf);
}
.showEvent {
  background: lightgray;
  background-color: lightgray;
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  color: green;
  font-family: emirates;
}